import React, { Component } from 'react';
import './Css/navbar.css'
class IndexBody extends Component {
  render(){
    const stylCont = {
      padding: "0",
      width: "100%",
      marginTop: "-60px",
      position: "absolute",
      zIndex: "-1"
    }
    return (
        <div class="container" style= {stylCont}>
          <div id="myCarousel" class="carousel slide" data-ride="carousel">
            <ol class="carousel-indicators" style = {{marginLeft: "-2%"}}>
              <li data-target="#myCarousel" data-slide-to="0" class="active"></li>
              <li data-target="#myCarousel" data-slide-to="1"></li>
              <li data-target="#myCarousel" data-slide-to="2"></li>
            </ol>
            <div class="carousel-inner">

              <div class="item active">
                <img src="https://www.gannett-cdn.com/presto/2018/11/29/PDTF/a6ec3756-89e0-4148-a9e0-c072c8daf5e5-Detroit_101707_PB_015.jpg" width="100%" alt="Los Angeles" />
                <div class="carousel-caption">
                  <h3>Green Management</h3>
                  <p>It is always so much fun!</p>
                </div>
              </div>

              <div class="item">
                <img src="http://www.technocrazed.com/wp-content/uploads/2015/12/Tree-wallpaper-to-use-as-background-1.jpg" alt="Chicago" width="100%" />
                <div class="carousel-caption">
                  <h3>Solid waste Management</h3>
                  <p>Thank you, Fellas!</p>
                </div>
              </div>

              <div class="item">
                <img src="https://treecanada.ca/wp-content/uploads/2018/02/Trees-2_opt.jpg" alt="New York" width="100%" />
                <div class="carousel-caption">
                  <h3>Sanitaion</h3>
                  <p>We love the Big Apple!</p>
                </div>
              </div>

            </div>

            <a class="left carousel-control" href="#myCarousel" data-slide="prev">
              <span class="glyphicon glyphicon-chevron-left"></span>
              <span class="sr-only">Previous</span>
            </a>
            <a class="right carousel-control" href="#myCarousel" data-slide="next">
              <span class="glyphicon glyphicon-chevron-right"></span>
              <span class="sr-only">Next</span>
            </a>
          </div>
        </div>

    )
  }
}
export default IndexBody;
