import React, { Component } from 'react';
import './Css/myHeader.css'
class MyHeader extends Component {
  render(){
    const JoinStyl = {marginRight: "-170px", marginLeft: "150px"}
    const Astly = {marginTop: "70px"}
    const tgLnstyl = {marginTop: "60px", paddingLeft: "20px", fontSize: "18px"}
    function scrollFunction() {
  if (document.body.scrollTop > 80 || document.documentElement.scrollTop > 80) {
    document.getElementById("navbar").style.padding = "00px 10px";
    document.getElementById("logo").style.fontSize = "25px";
    document.getElementById("tagln").style.display = "none";
    document.getElementById("joinstyl").style.display = "none";
    document.getElementById("navbar-right").style.marginTop = "-70px";

  } else {
    document.getElementById("navbar").style.padding = "40px 10px 0px";
    document.getElementById("logo").style.fontSize = "35px";
    document.getElementById("tagln").style.display = "block";
    document.getElementById("joinstyl").style.display = "block";
    document.getElementById("navbar-right").style.marginTop = "-130px";
  }
}
  return(
    <div>
      <div id="navbar" >
        <a href="#default" id="logo">CompanyLogo</a>
        <p id = "tagln" style = {tgLnstyl}>tag line</p>
        <div id="navbar-right">
          <a id = "joinstyl" className="active" href="#home"style={JoinStyl}>Join Us</a>
          <a href="#contact"style={Astly}>Tree Adaptation</a>
          <a href="#about" style={Astly}>Your Labour</a>
        </div>
        {window.onscroll = function() {scrollFunction()}}
      </div>
      <div style={{marginTop:"120px"}}></div>
      </div>
  )
}
}
export default MyHeader;
